@import "sizes";
@import url('https://fonts.googleapis.com/css?family=Merriweather');
@import url('https://fonts.googleapis.com/css?family=Lato');
.home-page{
  /*MAIN ANNIMATION SECTION*/
  font-family: 'Lato';
  .fixed-indicator{
    position: fixed;
    top:100px;
    height: 1px;
    width: 3px;
    background: black;
    z-index: 3;
  }
  .main-annimation{
    height:50vw;
    padding: 25px 0;
    display: table;
    width: 100%;
    .first-animation{
      margin-top: 30px;
      .left{
        display: table-cell;
        width: 50vw;
        height: 100%;
        .maps-container{
          position: relative;
          left: 8vw;
          img{
            width: 60%;
          }
          .africa-map{
            z-index: 4;
            position: absolute;
            top: 100px;
          }
          .africa-shadow{
            z-index: 3;
            opacity: 0;
            position: absolute;
            top:30px;
            left:30px;
            z-index: 3;
          }

        }

      }
      .right{
        /*
        display: table-cell;
        */
        width: 50vw;
        height: 100%;

        .white-div{
          height: 40vw;
          position: absolute;
          width: 1000px;
          right: 0px;
          background-color: white;
          z-index: 1;
        }

        .annim{
          height: 40vw;
          position: absolute;
          right: 0px;
          background: url('/img/annimation/right-1.png')no-repeat;
          background-size: contain;
          background-position: right;
          .anim-text{
            color: white;
            z-index:3;
            background: white;
            top: 6vw;
            right: 6vw;
            height: 25vw;
            display: block;
            width: 30vw;
            position: relative;
            p{
              font-size: 2vw;
            }
            span{
              font-size: 7vw;
            }
          }
        }
      }
    }
    .second-animation{
      
    }
  }
  /*END MAIN ANNIMATION SECTION*/
  font-family: lato;
  .video-conainer{

    width: 100%;
    video{
      width: 100%;
    }
    .text-video{
      position: absolute;
      top: 30vw;
      padding: 0 8%;
      margin: 0 auto;
      color: white;
      h2{
        font-family: 'Merriweather';
        font-size: 28px;
      }
      h4{
        font-weight: bold;
      }
      @include phone(){
        position: absolute;
        top: 10vw;
      }
      .right-text-video{
        position: absolute;
        bottom: 0;
        right: 50px;
        @include phone(){
          position: relative;
        }

        .goal-container{
          width: 100%;
          height: 15px;
          border-radius: 15px;
          border: solid 1px white;
          .goal-bar{
            max-width: 100%;
            width:0%;
            background: white;
            height: 100%;
            border-radius: 15px;
          }
          .goal-button{
            border: 1px solid white;
            background: transparent;
            color:white;
            float: right;
            margin-top: 20px;
            @include phone(){
              float: none;
              margin: 20px auto;
              width: 60%;
              display: block;
            }

            &:hover{
              background: rgba(173,173,173,0.60);
            }
          }
        }
      }
    }

  }

  .para{
    color: blue;

    @include phone(){
      color: green;
    }

    @include tablet-only(){
      color: red;
    }

  }

  .logo-home {
    position: absolute;
    top: 50px;
    left: 30px;
  }
}